const opts = function (opt) {
  const columns = [
    {
      title: '',
      key: 'amount',
      width: 110
    },
    {
      title: '',
      key: 'period',
      width: 110
    },
    {
      title: '',
      key: 'code',
      width: 100
    },
    {
      title: '',
      key: 'invest_amount',
      width: 110
    },
    {
      title: '',
      key: 'rate',
      width: 110
    },
    {
      title: '',
      key: 'name'
    },
    {
      title: 'ID',
      key: 'id',
      width: 100
    },
    {
      title: '',
      key: 'typeLable',
      width: 100
    },
    {
      title: '',
      key: 'statusLable',
      width: 100
    },
    {
      title: 'Action',
      key: 'action',
      fixed: 'right',
      width: 200,
      align: 'center',
      render: opt.actionRender
    }
  ]
  return columns
}

export default opts



