<template>
  <div class="add">
    <Button type="primary" style="width: 100%" @click="() => setVisible(true)"></Button>
    <Modal
      v-model="visible"
      title=""
      :loading="loading"
      @on-ok="asyncOK">
      <Form class="add-form" ref="addForm" :model="addParams" :rules="ruleValidate" inline label-position="top">
        <FormItem class="one" label="" prop="name">
          <Input class="inner-form" v-model="addParams.name" placeholder="" />
        </FormItem>
        <FormItem class="three" label="" prop="minAmount">
          <InputNumber class="inner-form" v-model="addParams.minAmount" :min="1" placeholder=""></InputNumber>
        </FormItem>
        <FormItem class="three" label="" prop="maxAmount">
          <InputNumber class="inner-form" v-model="addParams.maxAmount" :min="1" placeholder=""></InputNumber>
        </FormItem>
        <FormItem class="three" label="" prop="amount" style="margin-right: 0;">
          <InputNumber class="inner-form" v-model="addParams.amount" :min="1" placeholder=""></InputNumber>
        </FormItem>
        <FormItem class="two" label="" prop="code">
          <InputNumber class="inner-form" v-model="addParams.code" :min="1" placeholder=""></InputNumber>
        </FormItem>
        <FormItem class="two" label="" prop="period">
          <InputNumber class="inner-form" v-model="addParams.period" :min="1" placeholder=""></InputNumber>
        </FormItem>
        <FormItem  class="two" label="" prop="rate">
          <InputNumber class="inner-form" v-model="addParams.rate" :min="1" :step="0.01" placeholder=""></InputNumber>
        </FormItem>
        <FormItem class="two" label="" prop="repaymentSource">
          <Input class="inner-form" v-model="addParams.repaymentSource" placeholder="" />
        </FormItem>
        <FormItem class="one" label="" prop="loanUse">
          <Input class="inner-form" v-model="addParams.loanUse" placeholder="" />
        </FormItem>
        <FormItem class="one" label="" prop="riskPicture">
          <Input class="inner-form" v-model="addParams.riskPicture" placeholder="" />
        </FormItem>
        <FormItem class="one" label="" prop="description">
          <Input class="inner-form" type="textarea" v-model="addParams.description" placeholder="" />
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import {Button, Modal, Form, FormItem, Input, InputNumber} from 'iview'
export default {
  components: { Button, Modal, Form, FormItem, Input, InputNumber },
  data () {
    return {
      visible: false,
      loading: true,
      addParams: {
        code: 1,
        name: '',
        minAmount: 1,
        maxAmount: 1,
        amount: 1,
        period: 1,
        rate: 1,
        description: '',
        loanUse: '',
        repaymentSource: '',
        riskPicture: ''
      },
      ruleValidate: {
        code: [
          { required: true, message: '', trigger: 'blur', type: 'number' }
        ],
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        minAmount: [
          { required: true, message: '', trigger: 'blur', type: 'number' }
        ],
        maxAmount: [
          { required: true, message: '', trigger: 'blur', type: 'number' }
        ],
        amount: [
          { required: true, message: '', trigger: 'blur', type: 'number' }
        ],
        period: [
          { required: true, message: '', trigger: 'blur', type: 'number' }
        ],
        rate: [
          { required: true, message: '', trigger: 'blur', type: 'number' }
        ],
        description: [
          { required: true, message: '', trigger: 'blur' }
        ],
        loanUse: [
          { required: true, message: '', trigger: 'blur' }
        ],
        repaymentSource: [
          { required: true, message: '', trigger: 'blur' }
        ],
        riskPicture: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    ...mapState({
      createProducts: state => state.products.createProducts
    })
  },
  methods: {
    setVisible (val) {
      this.visible = val
      this.loading = false
      this.addParams = {
        code: 1,
        name: '',
        minAmount: 1,
        maxAmount: 1,
        amount: 1,
        period: 1,
        rate: 1,
        description: '',
        loanUse: '',
        repaymentSource: '',
        riskPicture: ''
      }
    },
    handleReset (name) {
      this.$refs.addForm.resetFields()
    },
    asyncOK () {
      this.$refs.addForm.validate(async (valid) => {
        if (valid) {
          console.log(this.addParams)
          await this.$store.dispatch('products/createProducts', {...this.addParams})
          if (this.createProducts) {
            this.$emit('get')
            this.$message.success('!')
            this.setVisible(false)
          } else {
            this.$message.error('!')
          }
        } else this.loading = false
      })
    }
  }
}
</script>

<style lang="less" scoped>
.add-form {
  .ivu-form-item {
    .inner-form {
      width: 100%;
    }
  }
  .one {
    width: 100%;
  }
  .two {
    width: calc(100% / 2 - 2px);
    margin-right: 0;
  }
  .three {
    width: calc(100% / 3 - 3px);
    margin-right: 0;
  }
}
</style>



